<template>
    <div :class="'page-container '+mobileClass">
        <div class="banner-display">
            <div class="banner-text">
                <div class="big">
                세계 최고의 화상솔루션<br>
                Zoom, Youtube의 확장팩<br>
                LiveGo를 경험하세요
                </div>
                <div class="small">
                누구에게나 도움이 되는 유연한 서비스, LiveGo를 통해<br>
                모두에게 매력적인 라이브 경험을 만들어 보세요!
                </div>
                <div class="button-box">
                    <v-btn class="whiteButton" @click="goDemo()" depressed style="max-width:280px !important;width:280px !important;">시작하기</v-btn>
                </div>
            </div>
            <div class="banner">
                <img src="../../../assets/images/img_mb1.svg">
            </div>
        </div>

    </div>
</template>
<script>
    import AUTH from '@/config/web/auth'
    import UTIL from '@/common/util'

    export default {
        name: 'siteMain',
        data() {
            return {
                util: UTIL,
                isLogin:false,
                mobileClass:'',
            };
        },
        created() {
            this.mobileClass = (this.$isMobile()) ? 'mobile' : '';
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.isLogin = true;
            }
        },
        methods: {
            goDemo() {
                if(this.isLogin) {
                    this.$router.push('/video/step')
                } else {
                    this.$router.push('/memberLogin')
                }
            },

        },


    }
</script>

<style lang="scss" scoped>
    .page-container {
        width: 100%;
        margin: 0;
        background: #1e90ff;
        height:calc(100vh);
        position: absolute;
        top:0;
        left:0;
        .banner-display {
            padding-top:180px;
            width: 1200px;
            margin: auto;
            display: flex;
            justify-content: space-between;
            .banner-text {
                padding-top:70px;
                color:#fff;
                .big {
                    font-size:25pt;
                }
                .small {
                    font-size:16pt;
                    padding:40px 0 60px 0;
                }
                .whiteButton {
                    background: none !important;
                    border:solid 1px #fff;
                    color:#fff !important;
                    min-width:300px !important;
                    min-height:70px !important;

                }
            }
            .banner {
                width: 588px;
                img {
                    width: 100%;
                }
            }
        }
        &.mobile {
            width: 100%;
            margin: 0;
            background: #1e90ff;
            height:auto;
            position:static;
            .banner-display {
                padding-top:80px;
                width:100%;
                display: block;
                .banner-text {
                    padding:30px;
                    color:#fff;
                    .big {
                        font-size:25px;
                    }
                    .small {
                        font-size:16px;
                        padding:40px 0 60px 0;
                    }

                }
                .banner {
                    width:70%;
                    padding:50px 0 80px 0;
                    margin:auto;
                    img {
                        width: 100%;

                    }
                }
            }
        }
    }

</style>
